html {
  background-image: url("./tic-tac-toe.png");
}

body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }

  h1 {
      display: block;
      margin-bottom: 5vh;
      background-color: white;
      padding: 1.5rem;
      border-radius: 10px;
      font-size: 2rem;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }

  .header {
      display: flex;
      justify-content: center;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 37px;
    font-weight: bold;
    line-height: 34px;
    height: 50px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 50px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  
  .game-info {
    margin-left: 20px;
    background-color: white;
    padding: 0.5rem;
    border-radius: 10px;
  }
  